import React from "react";
import { HeadProps, PageProps, graphql } from "gatsby";

import { IPrivacyPolicyQueryResult } from "../types/policies.type";

const PoliciesPage = ({ data }: PageProps<IPrivacyPolicyQueryResult>) => {
  const html = data.allMarkdownRemark?.nodes[0]?.html;

  return (
    <div className="bg-main-100  text-secondary-100">
      <div
        className="flex w-full max-w-7xl mx-auto privacy-policy flex-col gap-4 text-lg py-10"
        dangerouslySetInnerHTML={{ __html: html }}
      />
    </div>
  );
};

export default PoliciesPage;

export const Head = ({ data }: HeadProps<IPrivacyPolicyQueryResult>) => {
  const title = data.allMarkdownRemark?.nodes[0]?.frontmatter?.title;

  return <title>{title}</title>;
};

export const query = graphql`
  query ($lang: String, $policy: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: $policy }, language: { eq: $lang } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
        }
      }
    }
  }
`;
